  import Loading from '../public/Loading'
	import Head from "../public/head.vue"
  import Vue from "vue"
  import EXIF from "exif-js"
  Vue.use(EXIF)
	export default {
		data() {
			return {
			  api:this.GLOBAL.baseURL,
        updata:{
			    token: localStorage.getItem('token')
        },
				imgUrl:"",
				interInvite:"",
				emailInvite:"",
        WXurl:'',
        shreList:[
          {img:'../../static/img/static/tran/weixin.png',name:'微信'},
          {img:'../../static/img/static/tran/weibo.png',name:'微博'},
          {img:'../../static/img/static/tran/youx.png',name:'邮箱'},
          {img:'../../static/img/static/tran/copy.png',name:'复制连接'},
        ],
        clickIndex:0,
        shareContent:'',
        user:'',
        cover_image:[],
        YXnumber:'',
        imageUrl:"",
        editSearch:'搜索',
        asd:'',
        radio:'',
        reacetWho:'',
        emailCenter:'',
        isLoading:false,
        param:'',
        index:0,
        height:''
			};
		},
		components:{
			Head,
      Loading
		},
    methods:{
      goShare(){
        var top = document.getElementById('shareLocal').offsetTop
        document.documentElement.scrollTop = top
      },
		  getImg(){
        if(localStorage.getItem('userImg')){
          this.imgUrl = localStorage.getItem('userImg')
        }else{
          this.imgUrl = '../../static/img/static/user.png'
        }
      },
      getEWM(){
		    this.$http.post(this.api + '/home/Upload/getqrcode',{
		      token: localStorage.getItem('token'),
          url: this.asd,
        })
          .then(res=>{
           if(res.data.code == 1){
             this.WXurl = res.data.data.src
           }
          })
      },
      chooseShare(item,index){
		    if(this.clickIndex != index){
		      this.clickIndex = index
        }
      },
      share(val){
        if(val == 1){
          var p = {
            //将页面地址转成短域名， 并显示在内容文字后面。(可选， 允许为空)
            url: this.asd,
            //分享时所示的文字内容， 为空则自动抓取分享页面的title值(可选， 允许为空)
            title: this.shareContent,
            //自定义图片地址， 作为微博配图(可选， 允许为空)
            pic: this.imageUrl,
            //语言设置(zh_cn | zh_tw)(可选)
            language: 'zh_cn'
          };

          var s = [];
          for(var i in p) {
              s.push(i + '=' + encodeURIComponent(p[i] || ''));
          }
          var url = "http://service.weibo.com/share/share.php?" + s.join('&');
          window.open(url);
        }else{
          var reg = /^([a-zA-Z]|[0-9])(\w|\-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/
          if(!reg.test(this.reacetWho)){
            this.$message({type:'info',message: '请输入正确邮箱'})
          }else{
            this.$http.post(this.api + '/EmailShare',{
              token: localStorage.getItem('token'),
              toemail: this.reacetWho,
              title: this.emailCenter,
              content: this.shareContent,
              url: this.asd
            })
              .then(res=>{
                if(res.data.code == 1){
                  this.$message({
                    type: 'success',
                    message: '分享成功'
                  })
                  this.reacetWho = ''
                  this.emailCenter = ''
                  this.shareContent = ''
                }else if(res.data.code == 3){
                  this.share(val)
                }else{
                  alert(res.data.msg)
                }
              })
          }
        }
      },
      reload(res){
        this.user = res.data.data[0]
        this.asd =location.origin + '/share?userId='+res.data.data[0].user_id
        this.getEWM()
      },
      handleAvatarSuccess(res, file) {
	
        this.imageUrl = res.data.domain + res.data.image_url
      },
      beforeAvatarUpload(file) {
        const isJPG = file.type ==='jpg'||'jpeg'||'png';
        const isLt2M = file.size / 1024 / 1024 < 5;
        if (!isJPG) {
          this.$message.error('上传头像图片只能是 JPG/PNG 格式!');
        }
        if (!isLt2M) {
          this.$message.error('上传头像图片大小不能超过 5MB!');
        }
        return isJPG && isLt2M;
      },
      searchHome(){
        this.$router.push({
          name: 'homeSearch',
          query:{
            key: this.content,
            type:0
          }
      })
      },
      getVal(msg){
        this.content = msg
      },
      toYUedu(){
		    this.$router.push('/policy')
      },
      onCopy () {
        this.$message({
          message: `复制成功！`,
          type: 'success'
        });
      },
      onError () {
        this.$message({
          message: `复制失败！`,
          type: 'error'
        });
      },
      async seletI(event){
        this.isLoading = true
        await  this.xunhuan(event)

      },
      async xunhuan(event){
        var param = new FormData()
        if(event.target.files.length){
          if(event.target.files.length > 10){
            this.$message({
              type: 'info',
              message: '最多一次上传10张!'
            })
            this.isLoading = false
            this.$refs.file.value = ''
            return false
          }else{
            this.panduan(event.target.files,param)
          }
        }
      },
      panduan(fileAll,param){
        var Orientation = null;
        let _this = this
        if(_this.index ==  fileAll.length ){
          _this.index = 0
          _this.param = param
          _this.upload()
          return;
        }else{
          var file = fileAll[this.index]
          const isJap = file.type === 'image/jpeg' || file.type === 'image/jpg' || file.type === 'image/png'
          if (!isJap) {
            alert('请上传图片格式IPEG/PNG/JPG')
            _this.isLoading = false
            return false;
          } else {
            EXIF.getData(file, function () {
              EXIF.getAllTags(this);
              Orientation = EXIF.getTag(this, 'Orientation');
            });
            var oReader = new FileReader();
            oReader.onload = function (e) {

              var image = new Image();
              image.src = e.target.result;
              image.onload = function () {

                var expectWidth = this.naturalWidth;
                var expectHeight = this.naturalHeight;
                var canvas = document.createElement("canvas");
                var ctx = canvas.getContext("2d");
                canvas.width = expectWidth;
                canvas.height = expectHeight;
                ctx.drawImage(this, 0, 0, expectWidth, expectHeight);
                var base64 = null;
                if (Orientation != "" && Orientation != 1) {

                  switch (Orientation) {
                    case 6://需要顺时针（向左）90度旋转
//                      alert('需要顺时针（向左）90度旋转');
                      _this.rotateImg(this, 'left', canvas);
                      break;
                    case 8://需要逆时针（向右）90度旋转
//                      alert('需要顺时针（向右）90度旋转');
                      _this.rotateImg(this, 'right', canvas);
                      break;
                    case 3://需要180度旋转
//                      alert('需要180度旋转');
                      /* /!*this.rotateImg(this,'right',canvas);//转两次*/
                      /*    this.rotateImg(this,'right',canvas);*!/*/
                      _this.rotateImg(this, 'turntwo', canvas);
                      break;
                  }
                  base64 = canvas.toDataURL("image/jpeg", 0.8);
                  param.append('file[]', base64)
                }else{

                  base64 = canvas.toDataURL("image/jpeg", 0.8);
                  param.append('file[]', base64)
                }

                _this.index++;
                _this.panduan(fileAll,param)
              }
            };
            oReader.readAsDataURL(file)
          }
        }
      },
      rotateImg(img, direction,canvas){
        var min_step = 0;
        var max_step = 3;
        //var img = document.getElementById(pid);
        if (img == null)return;
        //img的高度和宽度不能在img元素隐藏后获取，否则会出错
        var height = img.height;
        var width = img.width;
        //var step = img.getAttribute('step');
        var step = 2;
        if (step == null) {
          step = min_step;
        }
        if (direction == 'right') {
          step++;
          //旋转到原位置，即超过最大值
          step > max_step && (step = min_step);
        } else {
          step--;
          step < min_step && (step = max_step);
        }
        //旋转角度以弧度值为参数
        var degree = step * 90 * Math.PI / 180;
        var ctx = canvas.getContext('2d');
        switch (step) {
          case 0:
            canvas.width = width;
            canvas.height = height;
            ctx.drawImage(img, 0, 0);
            break;
          case 1:
            canvas.width = height;
            canvas.height = width;
            ctx.rotate(degree);
            ctx.drawImage(img, 0, -height);
            break;
          case 2:
            canvas.width = width;
            canvas.height = height;
            ctx.rotate(degree);
            ctx.drawImage(img, -width, -height);
            break;
          case 3:
            canvas.width = height;
            canvas.height = width;
            ctx.rotate(degree);
            ctx.drawImage(img, -width, 0);
            break;
        }
      },
      upload(){
        this.param.append('token', localStorage.getItem('token'))
        this.$http.post(this.api + '/home/Upload/upload_many', this.param)
          .then(res=>{
            if(res.data.code == 1){
              let data = res.data.data
              for(let i =0;i<data.length; i++){
                this.imageUrl = data[i].domain + data[i].image_url
              }
              this.$refs.file.value = ''
              this.isLoading = false
            }else if(res.data.code == 3){
              this.upload()
            }else if(res.data.code == 0){
              alert(res.data.msg)
              this.isLoading = false
              this.$refs.file.value = ''
            }
          })
          .catch(error=>{
            this.$refs.file.value = ''
            this.isLoading = false
          })
      },
      getHeight(){
        let a = document.documentElement.clientHeight 
        this.height = a
   
      },
    },
    mounted() {
      this.getImg()
      this.getHeight()
    
    },
  }



